import React, { useRef, useEffect } from 'react';
import { HashRouter } from 'react-router-dom';
import Header from './components/Header/Header';
import Hero from './components/Hero/Hero';
import Projects from './components/Projects/Projects';
import Skills from './components/Skills/Skills';
import Blog from './components/Blog/Blog';
import Contact from './components/Contact/Contact';
import Footer from './components/Footer/Footer';
import './App.css';

const App = () => {
  const contactRef = useRef();

  // Scroll logging effect
  useEffect(() => {
    const handleScroll = () => {
      console.log('Scroll position:', window.scrollY);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // AOS initialization effect
  useEffect(() => {
    const initializeAnimations = async () => {
      try {
        // Check if AOS is properly loaded
        if (typeof AOS !== 'undefined') {
          AOS.init({
            duration: 800,              // Animation duration in ms
            once: true,                 // Only animate once
            mirror: false,              // Don't mirror on scroll up
            offset: 120,                // Trigger offset (px)
            easing: 'ease-out-quart',   // Animation easing
            delay: 100,                 // Delay between elements
            anchorPlacement: 'top-bottom' // Element placement trigger
          });

          // Refresh AOS after initial load
          window.addEventListener('load', () => {
            AOS.refresh();
          });
        } else {
          console.error('AOS library not loaded');
        }
      } catch (error) {
        console.error('AOS initialization error:', error);
        // Fallback to CSS animations if AOS fails
        document.body.classList.add('aos-fallback');
      }
    };

    // Initialize with a slight delay to ensure DOM readiness
    const initTimeout = setTimeout(initializeAnimations, 500);

    // Cleanup
    return () => {
      clearTimeout(initTimeout);
      window.removeEventListener('load', () => {
        if (typeof AOS !== 'undefined') AOS.refresh();
      });
      if (typeof AOS !== 'undefined') {
        AOS.refreshHard();
      }
    };
  }, []);

  return (
    <HashRouter>
      <div className="app-container font-inter bg-gray-50 text-gray-900 scroll-smooth">
        <Header contactRef={contactRef} />
        <main>
          <Hero />
          <Projects />
          <Skills />
          <Blog />
        </main>
        <Contact ref={contactRef} />
        <Footer />
      </div>
    </HashRouter>
  );
};

export default App;
